import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Paper,
  Box,
  Grid,
  TextField,
  Button,
  Switch,
  FormControlLabel,
  Alert,
  CircularProgress,
  Divider,
  Card,
  CardContent,
} from '@mui/material';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';

interface GameSettings {
  creatureAttackInterval: number;
  pointsPerSolve: number;
  streakBonus: number;
  enigmaticPeaceThreshold: number;
  isEnigmaticPeaceEnabled: boolean;
  isCreatureAttacksEnabled: boolean;
  maxDailyTeasers: number;
  maintenanceMode: boolean;
}

const AdminSettings: React.FC = () => {
  const { user } = useAuth();
  const [settings, setSettings] = useState<GameSettings>({
    creatureAttackInterval: 60,
    pointsPerSolve: 1,
    streakBonus: 0.5,
    enigmaticPeaceThreshold: 10,
    isEnigmaticPeaceEnabled: true,
    isCreatureAttacksEnabled: true,
    maxDailyTeasers: 1,
    maintenanceMode: false,
  });
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  useEffect(() => {
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      const response = await api.get('/admin/settings');
      setSettings(response.data);
      setError(null);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Error fetching settings');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field: keyof GameSettings) => (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const value = event.target.type === 'checkbox' 
      ? event.target.checked 
      : event.target.value;
    setSettings(prev => ({
      ...prev,
      [field]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);
    setError(null);
    setSuccess(null);

    try {
      await api.put('/admin/settings', settings);
      setSuccess('Settings updated successfully');
    } catch (err: any) {
      setError(err.response?.data?.message || 'Error updating settings');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          minHeight: '60vh',
        }}
      >
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" gutterBottom>
          Admin Settings
        </Typography>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        {success && (
          <Alert severity="success" sx={{ mb: 2 }}>
            {success}
          </Alert>
        )}

        <form onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            {/* Game Mechanics */}
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Game Mechanics
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Points per Solve"
                        type="number"
                        value={settings.pointsPerSolve}
                        onChange={handleChange('pointsPerSolve')}
                        inputProps={{ min: 1 }}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Streak Bonus (multiplier)"
                        type="number"
                        value={settings.streakBonus}
                        onChange={handleChange('streakBonus')}
                        inputProps={{ min: 0, step: 0.1 }}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Max Daily Teasers"
                        type="number"
                        value={settings.maxDailyTeasers}
                        onChange={handleChange('maxDailyTeasers')}
                        inputProps={{ min: 1 }}
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>

            {/* Creature System */}
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Creature System
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Creature Attack Interval (minutes)"
                        type="number"
                        value={settings.creatureAttackInterval}
                        onChange={handleChange('creatureAttackInterval')}
                        inputProps={{ min: 5 }}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <FormControlLabel
                        control={
                          <Switch
                            checked={settings.isCreatureAttacksEnabled}
                            onChange={handleChange('isCreatureAttacksEnabled')}
                          />
                        }
                        label="Enable Creature Attacks"
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>

            {/* Enigmatic Peace */}
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Enigmatic Peace
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        fullWidth
                        label="Peace Threshold (level)"
                        type="number"
                        value={settings.enigmaticPeaceThreshold}
                        onChange={handleChange('enigmaticPeaceThreshold')}
                        inputProps={{ min: 1 }}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <FormControlLabel
                        control={
                          <Switch
                            checked={settings.isEnigmaticPeaceEnabled}
                            onChange={handleChange('isEnigmaticPeaceEnabled')}
                          />
                        }
                        label="Enable Enigmatic Peace"
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>

            {/* System Settings */}
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    System Settings
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <FormControlLabel
                        control={
                          <Switch
                            checked={settings.maintenanceMode}
                            onChange={handleChange('maintenanceMode')}
                          />
                        }
                        label="Maintenance Mode"
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>

            <Grid item xs={12}>
              <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  size="large"
                  disabled={saving}
                >
                  {saving ? 'Saving...' : 'Save Settings'}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
};

export default AdminSettings; 